#init-modal-container {
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.87);
}

#modal-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto auto;
  padding: 20px;
  width: 80%;
}

#modal-title {
  font-size: 2rem;
  color: red;
  padding: 0.5rem;
}
