#menu-modal-container{
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
}

#menu-modal-circle{
    position: absolute;
    border: 5px dashed red;
    background-color: rgba(255, 255, 255, 0.116);
    border-radius: 100%;
    height: 65px;
    width: 65px;
}

#menu-modal-selection{
    position: absolute;
}

#character-selection{
    display: flex;
    flex-direction: column;
}